import React, { useEffect, useState } from 'react';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, Link, Divider } from '@material-ui/core';
import { FormProvider, RHFTextField, RHFSelect } from '../../../../../components/hook-form';
import CloseIcon from '@material-ui/icons/Close';
import useStyles from './styles';
import { IconButton } from '@mui/material';
import { addFocusSkill, updateFocusSkill, getAllFocusSkill } from '../../../../../store/actions/FocusSkillAdminActions';
import useUserRole from '../../../../../hooks/useUserRole';

const validationSchema = yup.object({
    title: yup.string('Enter Title').required('Title is required').trim()
});

export default function AddFocusSkill(props) {
    useUserRole({ roleList: ['ADMIN','SUPER_ADMIN'] });
    const [file, setFile] = useState(null);
    const { isOpen, setOpen, isEditData } = props;
    const [displayImage, setDisplayImage] = useState();
    const classes = useStyles();
    const dispatch = useDispatch();

    const initialValues = {
        title: isEditData?.title || ''
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });

    const { reset, handleSubmit, setValue } = methods;

    const handlePopupClose = () => {
        setOpen(false);
        reset();
    };
    const handleChange = function loadFile(event) {
        setDisplayImage(URL.createObjectURL(event.target.files[0]));
        setFile(event.target.files[0]);
    };

    const onSubmit = (data) => {
        const payload = {
            title: data?.title
        };

        const formData = new FormData();
        file && formData.append('image', file);
        if (isEditData) formData.append('focusSkillBody', JSON.stringify(payload));
        else formData.append('focusSkillBody', JSON.stringify(payload));

        if (isEditData) {
            dispatch(
                updateFocusSkill(formData, isEditData.id, () => {
                    dispatch(getAllFocusSkill());
                })
            );
            handlePopupClose();
        } else {
            dispatch(
                addFocusSkill(formData, () => {
                    dispatch(getAllFocusSkill());
                })
            );
            handlePopupClose();
        }
    };

    useEffect(() => {
        if (isEditData) {
            if (isEditData && Object.keys(isEditData)?.length) {
                setValue('title', isEditData?.title);

                setDisplayImage(isEditData?.imageUrl);

                fetch(isEditData?.imageUrl, { mode: 'no-cors' }).then(async (response) => {
                    const contentType = response.headers.get('content-type');
                    const blob = await response.blob();
                    const file = new File([blob], 'updatedImage.jpg', { contentType });
                    // setFile(file);
                });
            }
        }
    }, [isEditData]);

    return (
        <div>
            <Dialog open={isOpen} classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}> {isEditData ? 'Update Focus Skill' : 'Add Focus Skill'} </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <DialogContent>
                        <div>
                            <label className={classes.label}>
                                Image <span className={classes.mandatory}> {!isEditData ? '*' : ''} </span>
                            </label>
                            <div>
                                {!isEditData ? (
                                    <input
                                        type="file"
                                        onChange={handleChange}
                                        id="upload"
                                        name="image"
                                        accept="image/*"
                                        className={classes.imageInput}
                                        required
                                    />
                                ) : (
                                    <input
                                        type="file"
                                        onChange={handleChange}
                                        id="upload"
                                        name="image"
                                        accept="image/*"
                                        className={classes.imageInput}
                                    />
                                )}

                                <div>
                                    <label htmlFor="upload">
                                        <IconButton color="secondary" aria-label="upload picture" component="span">
                                            <img
                                                id="ImageCover"
                                                src={displayImage}
                                                className={classes.img}
                                                style={{ height: '200px', width: '200px' }}
                                            />
                                        </IconButton>
                                    </label>
                                    <label htmlFor="FocusSkillCover" />
                                </div>
                            </div>
                        </div>
                        <Grid container>
                            <Grid item xs={12}>
                                <label>
                                    Title <span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="title" placeholder="Title" />
                                <br /> <br />
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="secondary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" color="secondary" type="submit" className={classes.buttonSubmit}>
                            {isEditData ? 'Update' : 'Add'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
