import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import { GET_ALL_FOCUS_SKILL_START, GET_ALL_FOCUS_SKILL_SUCCESS, GET_ALL_FOCUS_SKILL_ERROR } from '../../constants';

const getAllFocusSkillStart = () => {
    return { type: GET_ALL_FOCUS_SKILL_START };
};
const getAllFocusSkillSuccess = (payload) => {
    return { type: GET_ALL_FOCUS_SKILL_SUCCESS, payload };
};
const getAllFocusSkillError = (error) => {
    return { type: GET_ALL_FOCUS_SKILL_ERROR, error };
};

const addFocusSkill = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS, 'POST', `subscriptions-service/focus-skills`, payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const updateFocusSkill = (payload, id, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS, 'PUT', `subscriptions-service/focus-skills/${id}`, payload, true)
            .then((res) => {
                if (res) {
                    dispatch(setMessage(res?.message));
                    successCB && successCB(res);
                }
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};
const deleteFocusSkill = (payload, id, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS, 'DELETE', `subscriptions-service/focus-skills/${id}`, payload, true)
            .then((res) => {
                dispatch(setMessage(payload?.deleted ? 'Deleted Successfully!' : 'Restore Successfully!'));

                console.log('deleted');
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};
const getAllFocusSkill =
    (rowsPerPage = 10, searchData = '', page = 1) =>
    (dispatch) => {
        dispatch(getAllFocusSkillStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS,
                'GET',
                `subscriptions-service/focus-skills?searchKey=${searchData}&pageNumber=${page}&pageSize=${rowsPerPage}`,
                true
            )
                .then((res) => {
                    const payload = {
                        allFocusSkills: res
                    };
                    dispatch(getAllFocusSkillSuccess(payload));
                })
                .catch((err) => {
                    dispatch(getAllFocusSkillError(err));
                    dispatch(setMessage(err?.data?.message));
                });
        } catch (error) {
            dispatch(getAllFocusSkillError('Something went wrong'));
        }
    };

const getAllDeletedFocusSkill =
    (rowsPerPage = 10, searchData = '', page = 1) =>
    (dispatch) => {
        dispatch(getAllFocusSkillStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS,
                'GET',
                `subscriptions-service/focus-skills/deleted?searchKey=${searchData}&pageNumber=${page}&pageSize=${rowsPerPage}`,
                true
            )
                .then((res) => {
                    const payload = {
                        allFocusSkills: res
                    };
                    dispatch(getAllFocusSkillSuccess(payload));
                })
                .catch((err) => {
                    dispatch(getAllFocusSkillError(err));
                    dispatch(setMessage(err?.data?.message));
                });
        } catch (error) {
            dispatch(getAllFocusSkillError('Something went wrong'));
        }
    };

export { updateFocusSkill, getAllFocusSkill, deleteFocusSkill, addFocusSkill, getAllDeletedFocusSkill };
